$green: #01b74f;
$red: #d72638;

* {
  box-sizing: border-box;
}

// nav
nav {
  font-family: "Souvenir";
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  img {
    width: 10%;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    li {
      float: right;
      padding: 10px;
      a {
        display: block;
        text-decoration: none;
        color: $green;
      }
    }
  }
}

//Menu item
.menuItem {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  img {
    width: 25%;
  }
  div.menuInfo {
    width: 70%;
    h3 {
      text-decoration: underline;
    }
  }
}

//Bottom Button
.bottomButton {
  position: fixed;
  bottom: 2%;
  right: 2%;
  width: 30%;
  padding-top: 30%;
  border-radius: 100%;
  background-color: $green;
  a {
    color: white;
    text-decoration: none;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }
}

//Backend Entry
div.backendEntry {
  margin: 30px 0;
  form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    label.line {
      margin: 10px 0;
      width: 100%;
      input,
      textarea {
        width: 100%;
        padding: 10px;
        border: none;
        border-bottom: 1px solid $green;
      }
    }

    label.radio {
      margin: 0 !important;
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      input,
      textarea {
        background-color: $green;
      }
    }
  }

  .bottomInputs {
    width: 100%;
    display: flex;
  }

  button {
    margin-top: 10px;
    color: $red;
  }
}

